body {
  background: #e8eaf6;
  background: -webkit-gradient(linear, left top, right top, from(#e8eaf6), to(#fbe9e7));
  background: -webkit-linear-gradient(left, #e8eaf6, #fbe9e7);
  background: -o-linear-gradient(left, #e8eaf6, #fbe9e7);
  background: linear-gradient(to right, #e8eaf6, #fbe9e7);
}



